import { Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import {
  GeneralDropdownVM,
  IRowFunctionVM,
  teacherByEduCompId,
} from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { ActivatedRoute, Router } from "@angular/router";
import { NgxSpinnerService } from "ngx-spinner";
import { MatTableDataSource } from "@angular/material/table";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { ITemplateListVM } from "app/dashboard/models/setup/exams-setup/exams";
import { TemplatesService } from "app/dashboard/services/setup/exams-setup/templates.service";
import { MessagesService } from "app/shared/services/messages.service";
@Component({
  selector: "app-templates-index",
  templateUrl: "./templates-index.component.html",
  styleUrls: ["./templates-index.component.css"],
})
export class TemplatesIndexComponent implements OnInit {
  myForm!: FormGroup;
  functionId: number;
  itemsCount: any;
  Templates: ITemplateListVM[] = [];
  Teachers: teacherByEduCompId[] = [];
  stages: GeneralDropdownVM[] = [];
  years: GeneralDropdownVM[] = [];
  subjects: GeneralDropdownVM[] = [];
  units: any = [];
  lessons: any = [];
  topics: any = [];
  EduCompId: any;
  submitted = false;
  // rowFunctions: IRowFunctionVM[];
  rowFunctions = [
    { functionName: "Publish" },
    { functionName: "Edit" },
    { functionName: "previewTemplate" },
    { functionName: "whatsApp" },
    { functionName: "Points" },
    { functionName: "Grades" },
    { functionName: "Delete" },
  ];
  displayedColumns: string[] = [
    "Name",
    "Teacher",
    "Subject",
    "Year",
    "publish",
    "actions",
  ];
  dataSource: MatTableDataSource<ITemplateListVM>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  constructor(
    private toastr: MessagesService,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    private authserv: AuthService,
    private fb: FormBuilder,
    private TemplatesService: TemplatesService,
    private spinner: NgxSpinnerService
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
            }
          });
      }
    });
  }

  ngOnInit() {
    this.EduCompId = this.authserv.getEduCompId();
    this.myForm = this.fb.group({
      yearId: [null],
      stageId: [null],
      subjectId: [],
      unitId: [],
      lessonId: [null],
      topicId: [null],
      page: [1],
    });
    this.getTemplates(1);

    this.myForm.valueChanges.subscribe((val) => {
      console.log("valueChanges", val);
    });
  }

  ngAfterViewInit() {
    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getTemplates(1);
    });

    this.authserv.branchId.subscribe((e) => {
      //this.branchId = e.branchId;
    });

    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  getTemplates(p: number) {
    this.spinner.show();
    this.myForm.controls["page"]?.setValue(p);
    this.TemplatesService.getTemplates(
      this.EduCompId,
      p,
      this.myForm.value
    ).subscribe((res: any) => {
      //  console.log('getTemplates',res);
      this.Templates = res.result;
      this.dataSource = new MatTableDataSource(res.result);
      this.itemsCount = res.TotalItemCount;
      this.spinner.hide();
      this.spinner.hide();
    });
  }

  PublishDepublishTemplate(templateId: any) {
    this.spinner.show();
    this.TemplatesService.PublishDepublishTemplate(templateId).subscribe(
      (res: any) => {
        if (res.returnString == "Done") {
          for (const obj of this.Templates) {
            if (obj.Id === templateId) {
              obj.isPublish = !obj.isPublish;
              break;
            }
          }
        } else {
          this.toastr.error(res.returnString);
        }
        this.spinner.hide();
      }
    );
  }

  openDeleateDialog(template: ITemplateListVM): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: template.Name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteTemplate(template);
      });
  }

  deleteTemplate(template: ITemplateListVM) {
    this.TemplatesService.deleteTemplate(template.Id).subscribe((res: any) => {
      let deletedIndex = this.Templates.findIndex((e) => e.Id == template.Id);
      this.Templates.splice(deletedIndex, 1);
      this.toastr.success("General.DeleteSuccessfully");
      this.getTemplates(1);
    });
  }

  GenerateExamFromTemplate(templateId: any) {
    this.spinner.show();
    this.TemplatesService.GenerateExamFromTemplate(templateId).subscribe(
      (res: any) => {
        if ((res.returnValue = 200 && res.examId)) {
          let url = `/dashboard/exams-setup/exam-preview/${res.examId}?templateId=${templateId}`;
          window.open(url, "_blank");
          // this.router
          //   .navigate([`/dashboard/exams-setup/exam-preview/${res.examId}`], {
          //     queryParams: { templateId: templateId },
          //   })
          //   .then((result) => {
          //     window.open(url, "_blank");
          //   });
        } else {
          this.toastr.error(res.returnString);
        }
        this.spinner.hide();
      }
    );
  }
}
