import { ActivatedRoute } from "@angular/router";
import { GeneralService } from "app/shared/services/General/general.service";
import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";

import { AuthService } from "app/shared/services/auth/auth.service";
import { NgxSpinnerService } from "ngx-spinner";
import { ToastrService } from "ngx-toastr";
 import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { IExamPointVM, IAddExamCustomePointsVM } from "app/dashboard/models/setup/points-setup/point";
import { PointsService } from "app/dashboard/services/setup/points-setup/points.service";

@Component({
  selector: "app-templates-custome-points",
  templateUrl: "./templates-custome-points.component.html",
  styleUrls: ["./templates-custome-points.component.scss"],
})
export class TemplatesCustomePointsComponent implements OnInit {
  Points: IExamPointVM[];
  myForm!: FormGroup;
  Submitted = false;
  p: number = 1;
  maxSize = 9;
  ItemsCount?: number;
  templateId: number;
  EduCompId: number;
  functionId: number;

  constructor(
    private fb: FormBuilder,
    private toastr: ToastrService,
    private authserv: AuthService,
    private route: ActivatedRoute,
    private PointsService: PointsService,
    private dialog: MatDialog,
    private spinner: NgxSpinnerService
  ) {
    route.paramMap.subscribe((params) => {
      this.templateId = Number(params.get("templateId"));
    });

    this.route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
    });
  }

  ngOnInit(): void {
    this.getTemplateCustomPoints(1);

    this.myForm = this.fb.group({
      ByQuestion: [false, [Validators.required]],
      from: [""],
      to: [""],
      value: ["", [Validators.required, CustomeValidator.minusNum]],
    });

    this.ByQuestionValidators(false);
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  addTemplateCustomPoint() {
    this.Submitted = true;

    let data: IAddExamCustomePointsVM = {
      itemId: this.templateId,
      ByQuestion: this.FormCtrls?.ByQuestion.value,
      from: this.FormCtrls?.from.value,
      to: this.FormCtrls?.to.value,
      value: this.FormCtrls?.value.value,
    };

    if (this.myForm.valid) {
      this.spinner.show();
      this.PointsService.addTemplateCustomPoint(data).subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.getTemplateCustomPoints(1);
          this.toastr.success("  ");
          this.Submitted = false;
          this.myForm.reset();
          this.FormCtrls?.ByQuestion.setValue(true);
        }
        this.spinner.hide();
      });
    }
  }

  getTemplateCustomPoints(p: Number) {
    this.spinner.show();
    this.PointsService.getTemplateCustomPoints<IExamPointVM>(
      p,
      this.templateId
    ).subscribe((res: any) => {
      if (res.returnValue == 1) {
        this.ItemsCount = res.itemsCount;
        this.Points = res.points;
      }
      this.spinner.hide();
    });
  }

  deleteExamCustomPoint(point: IExamPointVM) {
    this.spinner.show();
    this.PointsService.deleteExamCustomPoint(point.Id).subscribe((res: any) => {
      if (res.returnValue == 1) {
        let i = this.Points.findIndex((e) => e == point);
        this.Points.splice(i, 1);
        this.toastr.success(" ");
      }
      this.spinner.hide();
    });
  }

  openDeleateExamPointDialog(point: IExamPointVM): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: `${point.Value}: points`,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteExamCustomPoint(point);
      });
  }

  track(index: number, point: IExamPointVM): number {
    return point.Id;
  }

  ByQuestionValidators(ByQuestion: boolean) {
    if (ByQuestion == true) {
      this.FormCtrls.from.reset();
      this.FormCtrls.from.clearValidators();
      this.FormCtrls.to.reset();
      this.FormCtrls.to.clearValidators();
    } else {
      this.FormCtrls.from.setValidators([
        Validators.required,
        CustomeValidator.minusNum,
      ]);

      this.FormCtrls.to.setValidators([
        Validators.required,
        CustomeValidator.bigZero,
      ]);
    }
    this.FormCtrls.from.updateValueAndValidity();
    this.FormCtrls.to.updateValueAndValidity();
  }
}
