import { Component, OnInit, ViewChild } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import {
  IAvailableWhatsAppMsgVM,
  IGetExamWhatssAppMsgVM,
  IPostExamWhatssAppMsgVM,
} from "app/dashboard/models/setup/whatsapp-setup/whatsAppSetupVM";
import { WhatsAppSetupService } from "app/dashboard/services/setup/whatsapp-setup/whatsApp-setup.service";

import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { AuthService } from "app/shared/services/auth/auth.service";
import { MessagesService } from "app/shared/services/messages.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-exams-whatsapp",
  templateUrl: "./exams-whatsapp.component.html",
  styleUrls: ["./exams-whatsapp.component.scss"],
})
export class ExamsWhatsappComponent implements OnInit {
  myForm!: FormGroup;
  Submitted = false;
  IsTemplate: any;
  availableWhatsAppMsg: IAvailableWhatsAppMsgVM[] = [];
  examId: number;
  EduCompId: number;
  functionId: number;
  displayedColumns: string[] = [
    "msgTitle",
    "FromGrade",
    "ToGrade",
    "msgBody",
    "actions",
  ];
  dataSource: MatTableDataSource<IGetExamWhatssAppMsgVM>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  constructor(
    private fb: FormBuilder,
    private msg: MessagesService,
    private authService: AuthService,
    private route: ActivatedRoute,
    private dialog: MatDialog,
    private whatsAppService: WhatsAppSetupService,
    private spinner: NgxSpinnerService
  ) {
    route.paramMap.subscribe((params) => {
      this.examId = Number(params.get("examId"));
    });

    this.route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      this.IsTemplate = params.get("IsTemplate");
    });
  }

  ngOnInit(): void {
    this.EduCompId = Number(this.authService.getEduCompId());

    this.myForm = this.fb.group({
      Id: [null],
      whats_app_msg_id: ["", [Validators.required]],
      FromGrade: ["", [Validators.required, CustomeValidator.minusNum]],
      ToGrade: ["", [Validators.required, CustomeValidator.minusNum]],
    });

    this.getAvailableWhatsAppMsgToExam();
    this.getExamWhatsAppMsgs();
  }
  get FormCtrls() {
    return this.myForm.controls;
  }

  ngAfterViewInit() {
    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
    });
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  getAvailableWhatsAppMsgToExam() {
    this.spinner.show();
    this.whatsAppService
      .getAvailableWhatsAppMsgToExam<IAvailableWhatsAppMsgVM[]>(
        this.EduCompId,
        this.examId
      )
      .subscribe((res) => {
        this.availableWhatsAppMsg = res;
        // console.log("AvailableWhatsAppMsg", res);
        this.spinner.hide();
      });
  }

  getExamWhatsAppMsgs() {
    this.spinner.show();
    this.whatsAppService
      .getExamWhatsAppMsgs<IGetExamWhatssAppMsgVM[]>(this.examId)
      .subscribe((res) => {
        this.dataSource = new MatTableDataSource(res);
        console.log("getExamWhatsAppMsgs", res);
        this.spinner.hide();
      });
  }

  addEditWhatAppMsg() {
    this.Submitted = true;
    let body = {} as IPostExamWhatssAppMsgVM;
    body.Id = this.FormCtrls.Id?.value;
    body.whats_app_msg_id = this.FormCtrls.whats_app_msg_id?.value;
    body.exam_id = this.examId;
    body.FromGrade = this.FormCtrls.FromGrade?.value;
    body.ToGrade = this.FormCtrls.ToGrade?.value;
    body.IsTemplate = this.IsTemplate;
    if (this.myForm.valid) {
      this.spinner.show();
      this.whatsAppService
        .addEditExamWhatssAppMsg(body)
        .subscribe((res: any) => {
          this.Submitted = false;
          if (res.returnString == "Success") {
            this.getAvailableWhatsAppMsgToExam();
            this.getExamWhatsAppMsgs();
            body.Id == null
              ? this.msg.success("whatsApp.ExamMsgAdded") //add
              : this.msg.success("whatsApp.ExamMsgUpdated"); //update
            this.myForm.reset();
            this.FormCtrls.whats_app_msg_id.enable();
          } else this.msg.error(res.returnString);
          this.spinner.hide();
        });
    } else this.msg.warning("General.FormNotValid");
  }

  getMsgToEdit(whatsAppMsg: IGetExamWhatssAppMsgVM) {
    this.myForm.patchValue(whatsAppMsg);
    this.FormCtrls.whats_app_msg_id.setValue(whatsAppMsg.whatsAppMsgId);
    this.FormCtrls.whats_app_msg_id.disable();
    this.FormCtrls.FromGrade.setValue(whatsAppMsg.fromGrade);
    this.FormCtrls.ToGrade.setValue(whatsAppMsg.toGrade);
    console.log(whatsAppMsg);
  }

  openDeleateDialog(whatsAppMsg: IGetExamWhatssAppMsgVM): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: whatsAppMsg.msgTitle,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.removeWhatsApp(whatsAppMsg);
      });
  }

  removeWhatsApp(whatsAppMsg: IGetExamWhatssAppMsgVM) {
    this.whatsAppService
      .deleteExamWhatsAppMsgById(whatsAppMsg.Id)
      .subscribe((response: any) => {
        if (response.returnValue == 1) {
          //  console.log('deleted: ', response);
          let deletedIndex = this.dataSource.data.findIndex(
            (e) => e.Id == whatsAppMsg.Id
          );
          this.dataSource.data.splice(deletedIndex, 1);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
          this.getAvailableWhatsAppMsgToExam();
          this.msg.success("whatsApp.MsgDeleted");
        }
      });
  }
}
