import { Component, OnInit, ViewChild } from "@angular/core";
import { FormGroup, FormBuilder } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute, Router } from "@angular/router";

import {
  IQuestionsBankVM,
  IQBPreviousSearchVM,
} from "app/dashboard/models/setup/exams-setup/questionsBank";
import { CourceStructureService } from "app/dashboard/services/setup/educational-setup/cource-structure.service";
import { ExamsService } from "app/dashboard/services/setup/exams-setup/exams.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import {
  GeneralDropdownVM,
  IRowFunctionVM,
  teacherByEduCompId,
} from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { GeneralService } from "app/shared/services/General/general.service";
import { MessagesService } from "app/shared/services/messages.service";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-questions-index",
  templateUrl: "./questions-index.component.html",
  styleUrls: ["./questions-index.component.scss"],
})
export class QuestionsIndexComponent implements OnInit {
  EduCompId: any;
  branchId: any;
  itemsCount: number;
  myForm: FormGroup;
  functionId: number;
  // rowFunctions: IRowFunctionVM[];
  rowFunctions = [{ functionName: "Edit" }, { functionName: "Delete" }];
  Teachers: teacherByEduCompId[];
  stages: GeneralDropdownVM[];
  years: GeneralDropdownVM[];
  subjects: GeneralDropdownVM[];
  units: GeneralDropdownVM[];
  lessons: GeneralDropdownVM[];
  topics: GeneralDropdownVM[];
  displayedColumns: string[] = [
    "#",
    "questionID",
    "questionText",
    "questionType",
    "subjectName",
    "unitName",
    "lessonName",
    "topicName",
    "mark",
    "levelName",
    "actions",
  ];

  dataSource: MatTableDataSource<IQuestionsBankVM>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  previousSearch: IQBPreviousSearchVM;
  constructor(
    private fb: FormBuilder,
    private toastr: MessagesService,
    private authService: AuthService,
    private examService: ExamsService,
    private GeneralService: GeneralService,
    private courceStructureService: CourceStructureService,
    private spinner: NgxSpinnerService,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    private router: Router
  ) {
    route.queryParamMap.subscribe((params) => {
      this.previousSearch =
        this.router.getCurrentNavigation().extras.state?.previousSearch;
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authService
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
            }
          });
      }
    });
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      teacherUserId: [""],
      stageId: [""],
      yearId: [""],
      subjectId: [""],
      unitId: [""],
      lessonId: [""],
      topicId: [""],
      questionID: [""],
    });

    this.myForm.patchValue(this.previousSearch);
    this.EduCompId = this.authService.getEduCompId();
    this.branchId = this.authService.getBranchId();
    this.getQuestions(1);
  }

  get FormCtrl() {
    return this.myForm.controls;
  }

  ngAfterViewInit() {
    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getQuestions(1);
    });

    this.authService.branchId.subscribe((e) => {
      this.branchId = e.branchId;
    });
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  getQuestions(page: any) {
    this.spinner.show();
    this.examService
      .getQuestions(page, this.EduCompId, this.myForm.value)
      .subscribe((res: any) => {
        this.dataSource = new MatTableDataSource(res.returnModel);
        this.itemsCount = res.itemsCount;
        this.spinner.hide();
      });
  }

  deleteQuestion(question: IQuestionsBankVM) {
    this.spinner.show();
    this.examService
      .deleteQuestion(this.EduCompId, question.questionID)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          let i = this.dataSource.data.findIndex((e) => e === question);
          this.dataSource.data.splice(i, 1);
          this.dataSource.paginator = this.paginator;
          this.toastr.success("General.DeleteSuccessfully");
        }
        this.spinner.hide();
      });
  }

  openDeleateDialog(question: IQuestionsBankVM): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: `${question.questionID} - ${question.questionTypeEnglishName}`,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteQuestion(question);
      });
  }

  resetManual() {
    this.myForm.controls["teacherUserId"].setValue("");
    this.myForm.controls["yearId"].setValue("");
    this.myForm.controls["stageId"].setValue("");
    this.myForm.controls["subjectId"].setValue("");
    this.myForm.controls["unitId"].setValue("");
    this.myForm.controls["lessonId"].setValue("");
    this.myForm.controls["topicId"].setValue("");
  }
}
