import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
 import { environment } from "environments/environment";

@Injectable({
  providedIn: "root",
})
export class ExamsService {
   constructor(private http: HttpClient) {}

  getTeachersByEducompId(EduCompId: any) {
    return this.http.get(
      `${environment.baseUrl}Admin/QuestionsBank/${EduCompId}`
    );
  }

  downloadQuestion(Id: number) {
    return this.http.get(
      `${environment.baseUrl}Exam/downloadAttachPerfectAnswer}/${Id}`
    );
  }

  getQuestions(page: any, EduCompId: any, data: any) {
    return this.http.post(
      `${environment.baseUrl}Admin/QuestionsBankTable/${page}/${EduCompId}`,
      data
    );
  }

  getQuestionByID(id: any) {
    return this.http.get(`${environment.baseUrl}Admin/get-question-bank/${id}`);
  }

  addEditQuestion(eduCompID: any, data: any) {
    return this.http.post(
      `${environment.baseUrl}Admin/question-bank-add-edit/${eduCompID}`,
      data
    );
  }

  updateQuestion(Id: any, data: any) {
    return this.http.put(
      `${environment.baseUrl}Admin/QuestionsBank/${Id}`,
      data
    );
  }

  deleteQuestion(eduCompID: any, id: any) {
    return this.http.post(
      `${environment.baseUrl}Admin/removeQuestionBank/${eduCompID}/${id}`,
      null
    );
  }

  getSubjectsByTeacherId(teacherId: any) {
    return this.http.post(
      `${environment.baseUrl}Admin/get-lectures-by-teacher-query`,
      teacherId
    );
  }

  getAllNormalExams<T, TV>(obj: T) {
    return this.http.post<TV>(
      environment.baseUrl + "exam/returnRecentlyAddedExams",
      obj
    );
  }

  publishExam<TV>(examId: number) {
    return this.http.get<TV>(
      `${environment.baseUrl}exam/publish-depublish-exam/${examId}`
    );
  }

  deleteExam<TV>(id: number) {
    return this.http.delete<TV>(`${environment.baseUrl}Admin/RemoveExam/${id}`);
  }

  getAllCountries<T>() {
    return this.http.get<T>(environment.baseUrl + "country/return-countries");
  }

  getAllStages<T>(countryId: number) {
    return this.http.get<T>(
      `${environment.baseUrl}stage/return-stages/${countryId}`
    );
  }

  getAllEducationYears<T>(stageId: number) {
    return this.http.get<T>(
      `${environment.baseUrl}year/return-years/${stageId}`
    );
  }

  getAllSubjects<T>(educationYearId: number) {
    return this.http.get<T>(
      `${environment.baseUrl}subject/return-subjects/${educationYearId}`
    );
  }

  getAllTeachersBySublectId<T>(subjectId: number) {
    return this.http.get<T>(
      `${environment.baseUrl}teacher/return-teachers/${subjectId}`
    );
  }

  getAllUnitsBySublectId<T>(subjectId: number) {
    return this.http.get<T>(
      `${environment.baseUrl}unit/return-units/${subjectId}`
    );
  }

  getAllLessonsByUnitId<T>(unitId: number) {
    return this.http.get<T>(
      `${environment.baseUrl}lesson/return-lesson/${unitId}`
    );
  }

  getAllExamType<T>() {
    return this.http.get<T>(environment.baseUrl + "exam/return-exam-types");
  }

  getExamById<T>(id: number) {
    return this.http.get<T>(`${environment.baseUrl}exam/get-exam-by-id/${id}`);
  }

  getExamDetailsById<T>(examId: number) {
    return this.http.get<T>(`${environment.baseUrl}Exam/Details/${examId}`);
  }

  addExam<T, TY>(data: T) {
    return this.http.post<TY>(environment.baseUrl + "exam/add-exam", data);
  }

  getAllQuestionGroups<T>() {
    return this.http.get<T[]>(
      environment.baseUrl + "exam/return-question-groups"
    );
  }

  getAllQuestionHeaders<T>() {
    return this.http.get<T[]>(
      environment.baseUrl + "exam/return-question-heads"
    );
  }

  addExamGroupHeader<T, TY>(data: T) {
    return this.http.post<TY>(
      `${environment.baseUrl}exam/add-question-group-head`,
      data
    );
  }

  editExamGroupHeader<T, TY>(data: T) {
    return this.http.post<TY>(
      environment.baseUrl + "exam/EditQuestionHead",
      data
    );
  }

  deleteExamGroupHeader(id: number) {
    return this.http.get(
      `${environment.baseUrl}exam/delete-exam-head-by-id/${id}`
    );
  }

  getAllGroupHeaderByExamId<T>(id: number) {
    return this.http.get<T[]>(
      `${environment.baseUrl}exam/GetQuestionHeadGroupByExamId/${id}`
    );
  }

  getAllQuestionsByHeadId<T>(id: number) {
    return this.http.get<T>(
      `${environment.baseUrl}exam/GetExamQuestionsByQuestionHead/${id}`
    );
  }

  addQuestion<T>(data: any) {
    return this.http.post<T>(
      environment.baseUrl + "exam/add-exam-questions",
      data
    );
  }

  getQuestionForEditByID<T>(id: number) {
    return this.http.get<T>(
      `${environment.baseUrl}exam/return-question-details/${id}`
    );
  }

  editQuestion<T>(data: any) {
    return this.http.post<T>(
      environment.baseUrl + "exam/edit-exam-questions",
      data
    );
  }

  deleteExamQuestion<T>(id: number) {
    return this.http.delete<T>(
      `${environment.baseUrl}exam/RemoveExamQuestion/${id}`
    );
  }

  getExamPoints(examId: any, page: any) {
    return this.http.get(
      `${environment.baseUrl}exam/returnCustomPointExamByExamId/${examId}/${page}`
    );
  }

  addExamPoint(body: any) {
    return this.http.post(environment.baseUrl + "exam/CustomExamPoint", body);
  }

  deleteExamPoint(pointId: any) {
    return this.http.delete(
      `${environment.baseUrl}exam/DeleteCustomExamPoint/${pointId}`
    );
  }

  UploadExamStudentGrades(data: any) {
    return this.http.post(
      `${environment.baseUrl}Exam/postStudentsExamsGrades`,
      data
    );
  }

  getQuestionByCorrectionType(page: any, correctionStatus: any) {
    return this.http.get(
      `${environment.baseUrl}Exam/CorrectionIndexPaging/${page}/${correctionStatus}`
    );
  }

  submitCorrection(body: any) {
    return this.http.post(
      `${environment.baseUrl}/Exam/submitQuestionCorrection`,
      body
    );
  }

  getExamPreview(examId: any) {
    return this.http.get(environment.baseUrl + "Exam/Details/" + examId);
  }
}
