import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "environments/environment";

@Injectable({
  providedIn: "root",
})
export class PointsService {
  constructor(private http: HttpClient) {}

  //  Points Setup

  addPoints(educompId: number, data: any) {
    return this.http.post(
      `${environment.baseUrl}SetupPoints/SetupPoints/${educompId}`,
      data
    );
  }

  getPoints<T>(teacherUserId: number, typeId: number, eduCompId: number) {
    return this.http.get<T>(
      `${environment.baseUrl}SetupPoints/returnPointSystemByResourceId/${teacherUserId}/${typeId}/${eduCompId}`
    );
  }

  deletePoint(pointId: any) {
    return this.http.post(
      `${environment.baseUrl}SetupPoints/DeletePointSystemDetails/${pointId}`,
      null
    );
  }

  //######################################################################
  // Custome
  //######################################################################
  // Lecture Custome

  addLectureCustomPoint(data: any) {
    return this.http.post(
      `${environment.baseUrl}SetupPoints/CustomMaterialPoint`,
      data
    );
  }

  getLectureCustomPoints<T>(page: any, materialId: any) {
    return this.http.get<T>(
      `${environment.baseUrl}SetupPoints/returnCustomPointMaterialByMaterialId/${materialId}/${page}`
    );
  }

  deleteLectureCustomPoint(pointId: any) {
    return this.http.post(
      `${environment.baseUrl}SetupPoints/DeleteCustomMaterialPoint/${pointId}`,
      null
    );
  }

  //######################################################################
  // Exam Custome
  addExamCustomPoint(body: any) {
    return this.http.post(
      environment.baseUrl + "SetupPoints/CustomExamPoint",
      body
    );
  }

  getExamCustomPoints<T>(page: any, examId: any) {
    return this.http.get<T>(
      `${environment.baseUrl}SetupPoints/returnCustomPointExamByExamId/${examId}/${page}`
    );
  }

  deleteExamCustomPoint(pointId: any) {
    return this.http.post(
      `${environment.baseUrl}SetupPoints/DeleteCustomExamPoint/${pointId}`,
      null
    );
  }

  //######################################################################
  // Template Custome
  addTemplateCustomPoint(body: any) {
    return this.http.post(
      environment.baseUrl + "SetupPoints/CustomTemplatePoint",
      body
    );
  }

  getTemplateCustomPoints<T>(page: any, templateId: any) {
    return this.http.get<T>(
      `${environment.baseUrl}SetupPoints/returnCustomPointTemplateByTemplateId/${templateId}/${page}`
    );
  }
}
