import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "environments/environment";
@Injectable({
  providedIn: "root",
})
export class WhatsAppSetupService {
  constructor(private http: HttpClient) {}

  sendSingularWhatsAppMsg(data: any) {
    return this.http.post(
      `${environment.baseUrl}whats-app/send-singular-message`,
      data
    );
  }

  sendWhatsAppMsg(data: any) {
    return this.http.post(`${environment.baseUrl}whats-app/send-message`, data);
  }

  getAllWhatsAppMsg(body: any) {
    return this.http.post(
      `${environment.baseUrl}whats-app/GetAllWhatsAppSetups`,
      body
    );
  }

  RemoveWhatsAppMsg(whatsAppId: number) {
    return this.http.delete(
      `${environment.baseUrl}whats-app/delete-by-Id/${whatsAppId}`
    );
  }

  addEditWhatAppMsg<T>(body: T) {
    return this.http.post<T>(
      `${environment.baseUrl}whats-app/addEditWhatAppMsg`,
      body
    );
  }

  GetAllWhatsAppVariables() {
    return this.http.get(
      `${environment.baseUrl}whats-app/GetAllWhatsAppVariables`
    );
  }

  getWhatsMsgById(msgId: number) {
    return this.http.get(
      `${environment.baseUrl}whats-app/GetWhatsMsgById/${msgId}`
    );
  }

  getAvailableWhatsAppMsgToExam<T>(EduCompId: number, examId: number) {
    return this.http.get<T>(
      `${environment.baseUrl}whats-app/GetAvailableWhatsAppMsgToExam/${EduCompId}/${examId}`
    );
  }

  addEditExamWhatssAppMsg<T>(body: T) {
    return this.http.post<T>(
      `${environment.baseUrl}whats-app/addEditExamWhatsAppMsg`,
      body
    );
  }

  getExamWhatsAppMsgs<T>(examId: number) {
    return this.http.get<T>(
      `${environment.baseUrl}whats-app/GetExamWhatsAppMsgs/${examId}`
    );
  }

  deleteExamWhatsAppMsgById<T>(id: number) {
    return this.http.delete<T>(
      `${environment.baseUrl}whats-app/DeleteExamWhatsAppMsgById/${id}`
    );
  }
}
