import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "environments/environment";

@Injectable({
  providedIn: "root",
})
export class TemplatesService {
  constructor(private http: HttpClient) {}

  getTemplates(eduCompID: any, page: number, body: any) {
    return this.http.post(
      `${environment.baseUrl}Admin/getTemplates/${eduCompID}/${page}`,
      body
    );
  }

  getTemplateById<T>(templateId: any) {
    return this.http.get<T>(
      `${environment.baseUrl}template/returnTemplateById/${templateId}`
    );
  }

  deleteTemplate(id: any) {
    return this.http.get(
      `${environment.baseUrl}template/deleteTemplateById/${id}`
    );
  }

  PublishDepublishTemplate(id: any) {
    return this.http.post(
      `${environment.baseUrl}template/PublishDepublishTemplate/${id}`,
      null
    );
  }

  getPresets(eduCompId: any) {
    return this.http.get(
      `${environment.baseUrl}template/returnPresetsByEduCompId/${eduCompId}`
    );
  }

  addUpdateTemplate(data: any) {
    return this.http.post(environment.baseUrl + "template/addtemplate", data);
  }

  addTemplateDetails(data: any) {
    return this.http.post(environment.baseUrl + "AJAX/addTemplateDetail", data);
  }

  getQuestionDetails(templateId: any) {
    return this.http.get(
      `${environment.baseUrl}AJAX/returnTemplateDetails/${templateId}`
    );
  }

  DeleteDetails(id: any) {
    return this.http.get(
      `${environment.baseUrl}template/deleteTemplateDetailById/${id}`
    );
  }

  uploadTemplateStudentGrade(data: any) {
    let URL = environment.baseUrl + "Exam/postStudentsTemplatesGrades";
    return this.http.post(URL, data);
  }

  getTemplatePoints(templateId: any, page: any) {
    return this.http.get(
      `${environment.baseUrl}template/returnCustomPointTemplateByTemplateId/${templateId}/${page}`
    );
  }

  addTemplatePoint(body: any) {
    return this.http.post(
      environment.baseUrl + "template/CustomTemplatePoint",
      body
    );
  }

  deleteTemplatePoint(pointId: any) {
    return this.http.delete(
      `${environment.baseUrl}template/DeleteCustomTemplatePoint/${pointId}`
    );
  }

  GenerateExamFromTemplate(templateId: any) {
    return this.http.post(
      environment.baseUrl + "Exam/GenerateExamFromTemplate/" + templateId,
      null
    );
  }
}
