import { TranslateService } from "@ngx-translate/core";
import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { ToastrService } from "ngx-toastr";
 import { NgxSpinnerService } from "ngx-spinner";

import { AuthService } from "app/shared/services/auth/auth.service";
 import { ExamsService } from "app/dashboard/services/setup/exams-setup/exams.service";
import { TemplatesService } from "app/dashboard/services/setup/exams-setup/templates.service";
import { IExamVM, ITemlateVM } from "app/dashboard/models/setup/exams-setup/exams";
import { IUploadExamStudentGradeVM, IUploadTemplateStudentGradeVM } from "app/dashboard/models/setup/exams-setup/exam";
// import { IWhatsAppResponse } from "app/admin/models/whatsApp/whatsApp";

@Component({
  selector: "app-upload-exam-grades",
  templateUrl: "./upload-exam-student-grades.component.html",
  styleUrls: ["./upload-exam-student-grades.component.scss"],
})
export class UploadExamStudentGradesComponent implements OnInit {
  Exam: IExamVM;
  template: ITemlateVM;
  errors: any[];
  examId: number;
  templateId: number;
  functionId: number;
  EduCompId: number;
  errorMsg = false;
  file: any = null;
  constructor(
    private route: ActivatedRoute,
    private toastr: ToastrService,
    private ExamsService: ExamsService,
    private TemplatesService: TemplatesService,
    private AuthService: AuthService,
    private spinner: NgxSpinnerService,
    private translate: TranslateService
  ) {
    this.route.paramMap.subscribe((params) => {
      this.examId = Number(params.get("examId"));
      this.templateId = Number(params.get("templateId"));
    });

    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
    });

    console.log("examId", this.examId);
    console.log("templateId", this.templateId);
    if (this.examId > 0) this.getExamById(this.examId);
    if (this.templateId > 0) this.getTemplateById();
  }

  ngOnInit(): void {
    this.EduCompId = Number(this.AuthService.getEduCompId());
  }

  getExamById(examId: number) {
    this.spinner.show();
    this.ExamsService.getExamById(examId).subscribe((response: IExamVM) => {
      //   console.log("getExamById: ", response);
      if (response) {
        this.Exam = response;
      }
      this.spinner.hide();
    });
  }

  getTemplateById() {
    this.spinner.show();
    this.TemplatesService.getTemplateById<ITemlateVM[]>(
      this.templateId
    ).subscribe((res: any) => {
      console.log("getTemplateById", res);
      this.template = res;
      this.spinner.hide();
    });
  }

  UploadExamStudentGrades() {
    let obj = {} as IUploadExamStudentGradeVM;
    obj.examId = this.examId;
    obj.file = this.file;
    obj.examGrade = this.Exam.examGrade;
    // console.log("obj: ", obj);
    // console.log("this.myForm.value: ", this.myForm.value);

    if (obj.file == null) {
      this.translate.get("UploadStudentGrades.fileErr").subscribe((msg) => {
        this.toastr.warning(msg);
      });
      return;
    } else if (obj.examId == null) {
      this.translate.get("UploadStudentGrades.examIdErr").subscribe((msg) => {
        this.toastr.warning(msg);
      });
      return;
    } else if (obj.examGrade == null) {
      this.translate
        .get("UploadStudentGrades.examGradeErr")
        .subscribe((msg) => {
          this.toastr.warning(msg);
        });
      return;
    } else {
      this.spinner.show();
      this.ExamsService.UploadExamStudentGrades(obj).subscribe((res: any) => {
        if (res.isSendAllSuccess) {
          this.toastr.success("  ");
        } else if (!res.isSendAllSuccess) {
          this.toastr.error("    ");
          this.errors = res.errors;
        }
        this.spinner.hide();
      });
    }
  }

  uploadTemplateStudentGrades() {
    let obj = {} as IUploadTemplateStudentGradeVM;
    obj.templateId = this.templateId;
    obj.file = this.file;
    obj.examGrade = this.template.TemplateGrade;
    // console.log("obj: ", obj);
    // console.log("this.myForm.value: ", this.myForm.value);
    if (obj.file == null) {
      this.translate.get("UploadStudentGrades.fileErr").subscribe((msg) => {
        this.toastr.warning(msg);
      });
      return;
    } else if (obj.templateId == null) {
      this.translate.get("UploadStudentGrades.examIdErr").subscribe((msg) => {
        this.toastr.warning(msg);
      });
      return;
    } else if (obj.examGrade == null) {
      this.translate
        .get("UploadStudentGrades.examGradeErr")
        .subscribe((msg) => {
          this.toastr.warning(msg);
        });
      return;
    } else {
      this.spinner.show();
      this.TemplatesService.uploadTemplateStudentGrade(obj).subscribe(
        (res: any) => {
          if (res.isSendAllSuccess) {
            this.toastr.success("  ");
          } else if (!res.isSendAllSuccess) {
            this.toastr.error("    ");
            this.errors = res.errors;
          }
          this.spinner.hide();
        }
      );
    }
  }

  //Attache File
  onChange(event: any) {
    let fileName = <File>event.target.files[0].name;
    let fileSize = <File>event.target.files[0].size;
    let fileType = <File>event.target.files[0].type;
    let LastModified = <File>event.target.files[0].lastModified;
    let LastModifiedDate = <File>event.target.files[0].lastModifiedDate;

    if (event.target.files) {
      var reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      reader.onload = (event: any) => {
        let fileReder = event.target.result;
        let data = {
          FileAsBase64: fileReder,
          name: fileName,
          size: fileSize,
          type: fileType,
          LastModified: LastModified,
          LastModifiedDate: LastModifiedDate,
        };
        //console.log(data);
        this.file = data;
      };
    }
  }
}
